<template>
    <div>
      <el-page-header @back="goBack" content="">
      </el-page-header>
      <el-table
        :height="tableHeight"
        :data="tableData"
        style="width: 100%">
        <el-table-column
          fixed
          label=""
          type="index"
          width="50">
        </el-table-column>
        <el-table-column
          fixed
          label=""
          width="300">
          <template slot-scope="scope">
            <span>{{ scope.row.otherCompanyName }}</span>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          width="180">
          <template slot-scope="scope">
            <span >{{ scope.row.otherPrincipal }}</span>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          width="180">
          <template slot-scope="scope">
            <span >{{ scope.row.otherNumber }}</span>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          width="180">
          <template slot-scope="scope">
            <span >{{ scope.row.otherLng }}</span>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          width="180">
          <template slot-scope="scope">
            <span>{{ scope.row.otherLat }}</span>
          </template>
        </el-table-column>
        <el-table-column label="" fixed="right">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="handleEdit(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        background
        @current-change="handleCurrentChange"
        layout="total, prev, pager, next"
        :page-size="page.pageSize"
        :total="page.totalSize">
      </el-pagination>
      <el-dialog
        :title="otherEnterpriseCompanyName"
        :visible.sync="dialogVisible"
        width="50%">
        <div class="mytable">
          <table border="1" cellspacing="0" align="center">
            <tr>
              <th></th>
              <th></th>
              <th>L</th>
              <th>E</th>
              <th>C</th>
              <th>R</th>
              <th></th>
            </tr>
            <tr v-for="(item,index) in this.otherEnterpriseDetails" :key="index">
              <td>{{item.valuationCategory}}</td>
              <td>{{item.majorRiskCategory}}</td>
              <td>{{item.judgeL}}</td>
              <td>{{item.judgeE}}</td>
              <td>{{item.judgeC}}</td>
              <td>{{item.judgeR}}</td>
              <td v-if="item.riskLevel === ''" bgcolor="#F56C6C"></td>
              <td v-else-if="item.riskLevel === ''" bgcolor="orange"></td>
              <td v-else-if="item.riskLevel === ''" bgcolor="yellow"></td>
              <td v-else-if="item.riskLevel === ''" bgcolor="#409EFF"></td>
              <td v-else bgcolor="#67C23A"></td>
            </tr>
          </table>
          <p>
            
          </p>
          <p>
               1
          </p>
          <table border="1" cellspacing="0" align="center">
            <tr>
              <th colspan="2">(L)</th>
            </tr>
            <tr>
              <td></td>
              <td></td>
            </tr>
            <tr>
              <td>10</td>
              <td></td>
            </tr>
            <tr>
              <td>6</td>
              <td></td>
            </tr>
            <tr>
              <td>3</td>
              <td></td>
            </tr>
            <tr>
              <td>1</td>
              <td></td>
            </tr>
            <tr>
              <td>0.5</td>
              <td></td>
            </tr>
            <tr>
              <td>0.1</td>
              <td></td>
            </tr>
            <tr>
              <th colspan="2">(E)</th>
            </tr>
            <tr>
              <td></td>
              <td></td>
            </tr>
            <tr>
              <td>10</td>
              <td></td>
            </tr>
            <tr>
              <td>6</td>
              <td></td>
            </tr>
            <tr>
              <td>3</td>
              <td></td>
            </tr>
            <tr>
              <td>2</td>
              <td></td>
            </tr>
            <tr>
              <td>1</td>
              <td></td>
            </tr>
            <tr>
              <td>0.5</td>
              <td></td>
            </tr>
            <tr>
              <th colspan="2">(C)</th>
            </tr>
            <tr>
              <td></td>
              <td></td>
            </tr>
            <tr>
              <td>100</td>
              <td> 30  30  100  1 </td>
            </tr>
            <tr>
              <td>40</td>
              <td> 10 29  50 99  5000 1 </td>
            </tr>
            <tr>
              <td>15</td>
              <td> 3 9  10 49  1000  5000 </td>
            </tr>
            <tr>
              <td>7</td>
              <td> 3  10  1000 </td>
            </tr>
            <tr>
              <td>3</td>
              <td></td>
            </tr>
            <tr>
              <td>1</td>
              <td></td>
            </tr>
            <tr>
              <td colspan="2"> </td>
            </tr>
          </table>
          <p>RR2</p>
          <br>
          <p>2 </p>
          <table border="1" cellspacing="0" align="center">
            <tr>
              <th>(R)</th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
            <tr>
              <td>720</td>
              <td></td>
              <td></td>
              <td bgcolor="#F56C6C"></td>
            </tr>
            <tr>
              <td>720R240</td>
              <td></td>
              <td></td>
              <td bgcolor="orange"></td>
            </tr>
            <tr>
              <td>240R150</td>
              <td></td>
              <td></td>
              <td bgcolor="yellow"></td>
            </tr>
            <tr>
              <td>150R70</td>
              <td></td>
              <td></td>
              <td bgcolor="#409EFF"></td>
            </tr>
          </table>
        </div>
        <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
          <!---->
        <el-link :href="otherWordUrl" :disabled="linkIsDisabled">
          <el-button type="primary" @click="wordDownDialogVisible"></el-button>
        </el-link>
        </span>
      </el-dialog>
    </div>
</template>

<script>
export default {
  data () {
    return {
      tableHeight: window.innerHeight - 126,
      tableData: [],
      page: '',
      dialogVisible: false,
      otherEnterpriseCompanyName: '',
      otherEnterpriseDetails: [],
      otherWordUrl: '',
      linkIsDisabled: true
    }
  },
  mounted () {
    this.getAllOtherEnterprise(1)
  },
  methods: {
    // word
    wordDownDialogVisible () {
      if (this.otherWordUrl === '' || this.otherWordUrl === null) {
        this.$message.error('')
        this.dialogVisible = true
      } else {
        this.$message.success('')
        this.dialogVisible = false
      }
    },
    // linkwordDownDialogVisible ()
    async getOtherWordUrl (otherEnterpriseId) {
      const {data: res} = await this.$http.get('/get/other/word/url', {
        params: {
          'otherEnterpriseId': otherEnterpriseId
        }
      })
      if (res.result === 'SUCCESS') {
        if (res.data === '' || res.data === null) {
          this.otherWordUrl = ''
          this.linkIsDisabled = true
        } else {
          this.otherWordUrl = res.data
          this.linkIsDisabled = false
        }
      }
    },
    // 
    goBack () {
      this.$router.go(-1)
    },
    // 
    handleEdit (index, row) {
      this.otherEnterpriseCompanyName = row.otherCompanyName
      this.getAllOtherEnterpriseDetails(row.id)
      this.getOtherWordUrl(row.id)
      this.dialogVisible = true
    },
    // 
    handleCurrentChange (val) {
      // valpageNum 
      this.getAllOtherEnterprise(val)
    },
    // id
    async getAllOtherEnterpriseDetails (otherEnterpriseId) {
      const {data: res} = await this.$http.get('/get/other/enterprise/details/by/id', {
        params: {
          'otherEnterpriseId': otherEnterpriseId
        }
      })
      if (res.result === 'SUCCESS') {
        this.otherEnterpriseDetails = res.data
      }
    },
    // 
    async getAllOtherEnterprise (pageNum) {
      const {data: res} = await this.$http.get('/get/all/other/enterprise', {
        params: {
          'pageNum': pageNum,
          'pageSize': 10
        }
      })
      if (res.result === 'SUCCESS') {
        this.tableData = res.data.content
        this.page = res.data
      }
    }
  }
}
</script>

<style scoped>
  .el-page-header{
    margin-top: 1px;
    padding: 2px;
    background-color: white;
  }
  .el-pagination{
    background: white;
  }
  table{
    border-collapse: collapse;
    background: white;
  }
  .mytable{
    padding-bottom: 40px;
  }
</style>



