<template>
  <div>
    <el-page-header @back="goBack" :content="this.$route.query.riskLevel+''">
    </el-page-header>
    <el-table
      :height="tableHeight"
      :data="tableData"
      border
      style="width: 100%">
      <el-table-column
        fixed
        label=""
        type="index"
        width="50">
      </el-table-column>
      <el-table-column
        fixed
        prop="companyName"
        label=""
        width="240">
      </el-table-column>
      <el-table-column
        prop="principal"
        label=""
        width="120">
      </el-table-column>
      <el-table-column
        prop="informantNumber"
        label=""
        width="120">
      </el-table-column>
      <el-table-column
        prop="riskValue"
        label=""
        width="70">
      </el-table-column>
      <el-table-column
        prop="measure"
        label=""
        width="70">
      </el-table-column>
      <el-table-column
        prop="totalNumber"
        label=""
        width="70">
      </el-table-column>
      <el-table-column
        prop="majorDanger"
        label=""
        width="120">
      </el-table-column>
      <el-table-column
        prop="majorRiskName"
        label=""
        width="120">
      </el-table-column>
      <el-table-column
        prop="address"
        label=""
        width="300">
      </el-table-column>
      <el-table-column
        fixed="right"
        label=""
        width="100">
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      background
      @current-change="handleCurrentChange"
      layout="total, prev, pager, next"
      :page-size="page.pageSize"
      :total="page.totalSize">
    </el-pagination>
    <!---->
    <el-dialog
      :title="enterpriseInform.companyName"
      :visible.sync="dialogVisible"
      width="50%"
      :before-close="handleClose">
      <div class="mytable">
        <table border="1" cellspacing="0" align="center">
          <!---->
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          <!---->
          <tr v-for="(item,index) in tableDetails" :key="index">
            <td v-if="item.categoryId === 1" rowspan="1"></td>
            <td v-else-if="item.categoryId === 2"></td>
            <td v-else-if="item.categoryId === 3"></td>
            <td>{{item.influenceFactor}}</td>
            <td>{{item.determineFactor}}</td>
            <td>{{item.score}}</td>
            <td>{{item.check}}</td>
            <td><el-image
              style="width: 30px; height: 30px"
              :src="item.photo[0]"
              :preview-src-list="item.photo">
              <div slot="placeholder" class="image-slot">
                <span class="dot">...</span>
              </div>
              <div slot="error" class="image-slot">
                <p></p>
              </div>
            </el-image></td>
          </tr>
          <!---->
          <tr>
            <td></td>
            <td>{{enterpriseInform.riskValue}}</td>
            <td></td>
            <td>{{enterpriseInform.measure}}</td>
            <td colspan="2">=**</td>
          </tr>
          <tr>
            <td></td>
            <td>{{enterpriseInform.majorDanger}}</td>
            <td></td>
            <td colspan="3">{{enterpriseInform.majorRiskName}}</td>
          </tr>
          <tr>
            <td></td>
            <td>{{enterpriseInform.companyName}}</td>
            <td></td>
            <td>{{enterpriseInform.address}}</td>
            <td></td>
            <td>{{enterpriseInform.lng}}<br>{{enterpriseInform.lat}}</td>
          </tr>
          <tr>
            <td></td>
            <td>{{enterpriseInform.principal}}</td>
            <td></td>
            <td>{{enterpriseInform.informant}}<br>{{enterpriseInform.informantNumber}}</td>
            <td></td>
            <td>{{enterpriseInform.totalNumber}}</td>
          </tr>
        </table>
        <!---->
        <p></p>
        <p v-for="(item, index) in tableRiskDetails.computerExplainList" :key="index">{{item.computerCount}}</p>
        <!---->
        <h5></h5>
        <table border="1" cellspacing="0" align="center">
          <tr>
            <th>(R)</th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          <tr>
            <td>{{tableRiskDetails.rmajorRisk}}</td>
            <td></td>
            <td></td>
            <td bgcolor="#F56C6C"></td>
          </tr>
          <tr>
            <td>{{tableRiskDetails.rlargerRisk}}</td>
            <td></td>
            <td></td>
            <td bgcolor="orange"></td>
          </tr>
          <tr>
            <td>{{tableRiskDetails.rgeneralRisk}}</td>
            <td></td>
            <td></td>
            <td bgcolor="yellow"></td>
          </tr>
          <tr>
            <td>{{tableRiskDetails.rlowRisk}}</td>
            <td></td>
            <td></td>
            <td bgcolor="#409EFF"></td>
          </tr>
        </table>
      </div>
      <span slot="footer" class="dialog-footer">
      <el-button @click="dialogVisible = false"></el-button>
        <!---->
      <el-link :href="wordUrl" :disabled="linkIsDisabled">
      <el-button type="primary" @click="wordDownDialogVisible"></el-button>
      </el-link>
  </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      tableHeight: window.innerHeight - 126,
      wordUrl: '',
      linkIsDisabled: true,
      tableDetails: [],
      tableRow: 0,
      url: '',
      srcList: [],
      dialogVisible: false,
      myHeight: window.innerHeight - 123,
      page: '',
      tableData: [],
      enterpriseInform: '',
      tableRiskDetails: ''
    }
  },
  mounted () {
    this.getAllMajorEnterprise(1)
  },
  methods: {
    // 
    wordDownDialogVisible () {
      if (this.wordUrl === '' || this.wordUrl === null) {
        this.$message.error('')
        this.dialogVisible = true
      } else {
        this.$message.success('')
        this.dialogVisible = false
      }
    },
    // linkwordDownDialogVisible ()
    async getWordUrl (enterpriseId) {
      const {data: res} = await this.$http.get('/get/word/url', {
        params: {
          'enterpriseId': enterpriseId
        }
      })
      if (res.result === 'SUCCESS') {
        if (res.data === '' || res.data === null) {
          this.wordUrl = ''
          this.linkIsDisabled = true
        } else {
          this.wordUrl = res.data
          this.linkIsDisabled = false
        }
      }
    },
    // 
    async getTableRiskDetails (tableId) {
      const {data: res} = await this.$http.get('/get/table/risk/details/by/id', {
        params: {
          'tableId': tableId
        }
      })
      if (res.result === 'SUCCESS') {
        this.tableRiskDetails = res.data
      }
    },
    // 
    async getTableDetailsByTableId (row) {
      this.enterpriseInform = row
      const {data: res} = await this.$http.get('/get/table/details/by/id', {
        params: {
          'tableId': row.companyType,
          'enterpriseId': row.id
        }
      })
      if (res.result === 'SUCCESS') {
        this.tableDetails = res.data
        this.tableDetails.forEach((item, index) => {
          item.photo = (item.photo || '').split(',')
        })
      }
    },
    handleClose (done) {
      this.$confirm('')
        .then(_ => {
          done()
        })
        .catch(_ => {
        })
    },
    // 
    handleCurrentChange (val) {
      // valpageNum 
      this.getAllMajorEnterprise(val)
    },
    // 
    async getAllMajorEnterprise (pageNum) {
      const {data: res} = await this.$http.get('/get/all/risk/enterprise', {
        params: {
          'riskLevel': this.$route.query.riskLevel,
          pageNum: pageNum,
          pageSize: 8
        }
      })
      if (res.result === 'SUCCESS') {
        this.page = res.data
        this.tableData = res.data.content
      }
    },
    goBack () {
      this.$router.go(-1)
    },
    // table
    handleClick (row) {
      // this.$router.push({path: '/enterpriseTableDetails', query: {row}})
      this.getTableDetailsByTableId(row)
      this.getTableRiskDetails(row.companyType)
      this.getWordUrl(row.id)
      this.dialogVisible = true
    }
  }
}
</script>

<style scoped>
  .el-page-header{
    margin-top: 1px;
    padding: 2px;
    background-color: white;
  }
  .el-page-header__left .el-page-header__title{
  }
  table{
    border-collapse: collapse;
    background: white;
  }
  .mytable{
    padding-bottom: 40px;
  }
  .el-pagination{
    background: white;
  }
</style>



