<template>
  <div>
    <div>
      <el-steps :active="active" finish-status="success" simple style="margin-top: 1px">
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title="/"></el-step>
      </el-steps>
    </div>
    <div>
      <!--<el-button  type="primary"  @click="dialogFormVisible = true" ></el-button>-->
      <el-dialog title="" :visible.sync="dialogFormVisible" :before-close="handleClose">
        <el-form :model="form" ref="form" :rules="rules">
          <el-form-item label="" :label-width="formLabelWidth" prop="tableName">
            <el-input v-model="form.tableName" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button @click="downDialogFormVisible"> </el-button>
            <el-button type="primary" @click="upDialogFormVisible('form')"> </el-button>
          </el-form-item>
        </el-form>
        <!--<div slot="footer" class="dialog-footer">
          <el-button @click="downDialogFormVisible"> </el-button>
          <el-button type="primary" @click="upDialogFormVisible"> </el-button>
        </div>-->
      </el-dialog>
    </div>
    <el-form :model="ruleForm" :rules="rules" :disabled="ruleForm.isDisabled" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <div class="influenceFactor-css">
      <el-form-item label="" prop="influenceFactor">
        <el-input v-model="ruleForm.influenceFactor"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitInfluenceFactor('ruleForm')"></el-button>
      </el-form-item>
      </div>
    </el-form>
    <el-form ref="sizeForm" :model="sizeForm" :disabled="sizeForm.isDisabled" label-width="100px" :rules="rules">
      <div class="form-css">
        <el-form-item label="" prop="determineFactor">
          <el-input v-model="sizeForm.determineFactor"></el-input>
        </el-form-item>
        <el-form-item label="" prop="score">
          <el-input v-model.number="sizeForm.score" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit('sizeForm')" autofocus></el-button>
        </el-form-item>
      </div>
    </el-form>
    <div>
      <el-card class="box-card">
        <h3>{{sizeForm.tableName}}</h3>
        <div v-for="(item,index) in this.formList" :key="index" class="text item">
          {{item.influenceFactor}}
          {{item.determineFactor}}
          {{item.score}}
        </div>
        <el-button type="primary" class="button" :disabled="isDisabled" @click="nextStep" >{{this.step}}</el-button>
      </el-card>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      isDisabled: true,
      step: '',
      active: 0,
      dialogFormVisible: true,
      form: {
        tableName: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      formLabelWidth: '120px',
      ruleForm: {
        influenceFactor: '',
        isDisabled: false
      },
      sizeForm: {
        tableId: '',
        tableName: '',
        categoryId: 1,
        influenceFactor: '',
        determineFactor: '',
        score: '',
        photo: '',
        isDisabled: true
      },
      formList: [],
      rules: {
        influenceFactor: [
          {required: true, message: '', trigger: 'blur'}
        ],
        tableName: [
          {required: true, message: '', trigger: 'blur'}
        ],
        determineFactor: [
          {required: true, message: '', trigger: 'blur'}
        ],
        score: [
          {required: true, message: '', trigger: 'blur'},
          {type: 'number', message: ''}
        ]
      }
    }
  },
  methods: {
    submitInfluenceFactor (formName) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          const {data: res} = await this.$http.post('/insert/table/influence', {
            tableId: this.sizeForm.tableId,
            tableName: this.sizeForm.tableName,
            categoryId: this.sizeForm.categoryId,
            influenceFactor: this.ruleForm.influenceFactor
          })
          console.log(res)
          if (res.result === 'SUCCESS') {
            // this.sizeForm.influenceFactor = res.influenceFactor
            this.sizeForm.isDisabled = false
            this.$message.success('')
          }
        } else {
          this.$message.error('')
          return false
        }
      })
    },
    // 
    nextStep () {
      // 
      this.formList.splice(0, this.formList.length)
      // 
      this.ruleForm.isDisabled = false
      this.sizeForm.isDisabled = true
      this.isDisabled = true
      // 
      if (this.sizeForm.categoryId++ === 3) {
        this.isDisabled = false
        this.step = ''
      }
      if (this.active++ > 2) {
        this.$message.success('')
        this.$router.push({path: '/tableinsert'})
        location.reload()
      }
    },
    // 
    handleClose (done) {
      this.$confirm('')
        .then(_ => {
          done()
          this.$router.push({path: '/home'})
        })
        .catch(_ => {
        })
    },
    // 
    downDialogFormVisible () {
      this.dialogFormVisible = false
      this.$router.push({path: '/home'})
    },
    // 
    upDialogFormVisible (formName) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          // id
          const {data: res} = await this.$http.post('/insert/table', {
            tableName: this.form.tableName
          })
          if (res.result === 'SUCCESS') {
            // 
            this.sizeForm.tableId = res.data.id
            this.sizeForm.tableName = res.data.tableName
            this.$message.success('')
            // 
            this.dialogFormVisible = false
          } else {
            this.$message.error('')
            // 
            this.dialogFormVisible = true
          }
        } else {
          this.$message.error('')
          return false
        }
      })
    },
    // 
    onSubmit (formName) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          const {data: res} = await this.$http.post('/insert/table/details', {
            tableId: this.sizeForm.tableId,
            tableName: this.sizeForm.tableName,
            categoryId: this.sizeForm.categoryId,
            influenceFactor: this.ruleForm.influenceFactor,
            determineFactor: this.sizeForm.determineFactor,
            score: this.sizeForm.score,
            photo: this.sizeForm.determineFactor + ''
          })
          if (res.result === 'SUCCESS') {
            // list
            this.isDisabled = false
            // this.ruleForm.isDisabled = true
            this.formList.push(res.data)
            this.$message.success('')
          } else {
            this.$message.error('')
          }
        } else {
          this.$message.error('')
          return false
        }
      })
    }
  }
}
</script>

<style scoped>
.form-css{
  float: left;
}
.text {
  font-size: 14px;
}
.item {
  padding: 2px 0;
}

.box-card {
  width: 480px;
  float: right;
  margin-right: 20px;
}
.influenceFactor-css{
  display: flex;
}
</style>



